.projects-area {
	width: 160%;
	display: flow-root;
}

a.project-image,
a.project-image:visited {
	height: 373px;
	margin-bottom: 30px;
	display: block;
	border: 0;

	&::before {
		content: "";
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: map-get($map: $colors, $key: red15);
		transition: all 0.3s ease-in-out;
		opacity: 0;
	}

	&:hover {
		&::before {
			opacity: 1;
		}
	}
}

.single-project {
	width: 39%;
	max-width: 512px;
	float: left;
	margin-right: 40px;

	&:last-child {
		margin-right: 0;
	}
}

.project-desc {
	h3 {
		margin-bottom: 20px;

		a,
		a:visited {
			color: map-get($map: $colors, $key: white);
			border-bottom: 0;

			&:hover {
				color: map-get($map: $colors, $key: red);
			}
		}
	}
}

.project-cat {
	a.button.small,
	a.button.small:visited {
		padding: 7px 15px 9px;
		margin: 0 10px 0 0;

		@include font(st, reg, weight);
	}
}

.projects-ctn {
	padding-top: 120px;
}

.work-items {
	gap: var(--ed_space_7);
}

.work-item {
	// width: calc(50% - var(--ed_space_6));
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;

	// &:nth-child(2n + 2) {
	// 	padding-top: var(--ed_space_6);
	// }
}

.work-item-image {
	margin-bottom: var(--ed_space_4);
	border-radius: 24px;
	overflow: hidden;
	width: 50%;

	a.no-link-style,
	a.no-link-style:visited {
		height: 100%;

		img {
			transition: all 0.3s ease-in-out;
		}

		&:before {
			content: "";
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			background-color: map-get($map: $colors, $key: red2);
			transition: all 0.3s ease-in-out;
			opacity: 0;
			z-index: 2;
		}

		&:hover {
			&::before {
				opacity: 0.5;
			}

			img {
				transform: scale(1.15);
			}
		}
	}
}

.work-item-content {
	width: 45%;
}

a.work-title,
a.work-title:visited {
	padding-bottom: 0;

	&:hover {
		color: map-get($map: $colors, $key: red);
	}
}

.white-ctn {
	a.work-title,
	a.work-title:visited {
		&:hover {
			color: map-get($map: $colors, $key: red2);
		}
	}
}

.work-exerpt {
	margin-top: 16px;
}
