:root {

	--space_base_unit: 0.125rem; // 2px

	/* Mobile Spacer Value */

	--ed_space_1_min: var(--space_base_unit) * 3;
	/* 6px */
	--ed_space_2_min: var(--space_base_unit) * 5;
	/* 10px */
	--ed_space_3_min: var(--space_base_unit) * 10;
	/* 20px */
	--ed_space_4_min: var(--space_base_unit) * 12;
	/* 24px */
	--ed_space_5_min: var(--space_base_unit) * 18;
	/* 36px */
	--ed_space_6_min: var(--space_base_unit) * 24;
	/* 48px */
	--ed_space_7_min: var(--space_base_unit) * 48;
	/* 96px */
	--ed_space_8_min: var(--space_base_unit) * 32;
	/* 64px */
	--ed_space_9_min: var(--space_base_unit) * 36;
	/* 72px */
	--ed_space_10_min: var(--space_base_unit) * 40;
	/* 80px */

	/* Desktop Spacer Value */

	--ed_space_1_max: var(--space_base_unit) * 3;
	/* 6px */
	--ed_space_2_max: var(--space_base_unit) * 6;
	/* 12px */
	--ed_space_3_max: var(--space_base_unit) * 12;
	/* 24px */
	--ed_space_4_max: var(--space_base_unit) * 16;
	/* 32px */
	--ed_space_5_max: var(--space_base_unit) * 24;
	/* 48px */
	--ed_space_6_max: var(--space_base_unit) * 48;
	/* 96px */
	--ed_space_7_max: var(--space_base_unit) * 96;
	/* 192px */
	--ed_space_8_max: var(--space_base_unit) * 64;
	/* 128px */
	--ed_space_9_max: var(--space_base_unit) * 72;
	/* 144px */
	--ed_space_10_max: var(--space_base_unit) * 80;
	/* 160px */

	// Fluid Spacers

	--ed_space_1: clamp(var(--ed_space_1_min),
			0vw + 0.375rem,
			var(--ed_space_1_max));
	--ed_space_2: clamp(var(--ed_space_2_min),
			0.231vw + 0.565rem,
			var(--ed_space_2_max));
	--ed_space_3: clamp(var(--ed_space_3_min),
			0.462vw + 1.13rem,
			var(--ed_space_3_max));
	--ed_space_4: clamp(var(--ed_space_4_min),
			0.924vw + 1.261rem,
			var(--ed_space_4_max));
	--ed_space_5: clamp(var(--ed_space_5_min),
			1.386vw + 1.891rem,
			var(--ed_space_5_max));
	--ed_space_6: clamp(var(--ed_space_6_min),
			5.543vw + 1.566rem,
			var(--ed_space_6_max));
	--ed_space_7: clamp(var(--ed_space_7_min),
			11.085vw + 3.132rem,
			var(--ed_space_7_max));
	--ed_space_8: clamp(var(--ed_space_8_min),
			7.072vw + 2.343rem,
			var(--ed_space_8_max));
	--ed_space_9: clamp(var(--ed_space_9_min),
			7.956vw + 2.635rem,
			var(--ed_space_9_max));
	--ed_space_10: clamp(var(--ed_space_10_min),
			8.84vw + 2.928rem,
			var(--ed_space_10_max));

}

.s-192 {
	height: var(--ed_space_7);
}

.s-96 {
	height: var(--ed_space_6);
}

.s-48 {
	height: var(--ed_space_5);
}

.s-32 {
	height: var(--ed_space_4);
}

.s-24 {
	height: var(--ed_space_3);
}

.s-12 {
	height: var(--ed_space_2);
}


.kwayy-html-sitemap-post-title {
	margin-bottom: var(--ed_space_6);
}
